'use client';

import { motion } from 'framer-motion';

export default function TeamOverview({ teamMembers = [] }) {
  const initials = (name = '') => {
    const [first = '', second = ''] = name.split(' ');
    return `${first.charAt(0)}${second.charAt(0)}`.toUpperCase() || name.charAt(0).toUpperCase();
  };

  const getProgressColor = (workload) => {
    if (workload === 100) return 'bg-red-500';
    if (workload >= 80) return 'bg-orange-500';
    if (workload >= 60) return 'bg-yellow-500';
    return 'bg-green-500';
  };

  return (
    <div className="grid gap-6 md:grid-cols-2 lg:grid-cols-3">
      {teamMembers.length > 0 ? teamMembers.map((member, index) => {
        const tasks = Array.isArray(member.tasks) ? member.tasks.length : member.taskCount || 0;
        const capacity = member.capacity || 5;
        const workload = capacity ? Math.min(100, Math.round((tasks / capacity) * 100)) : 0;

        return (
          <motion.div
            key={member._id}
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.3, delay: index * 0.1 }}
            whileHover={{ y: -4, transition: { duration: 0.2 } }}
            className="bg-white rounded-2xl p-6 border border-slate-200 shadow-sm hover:shadow-md transition-shadow"
          >
            <div className="flex items-center gap-4 mb-5">
              <div className="w-16 h-16 rounded-full bg-gradient-to-br from-slate-100 to-slate-200 flex items-center justify-center text-lg font-semibold text-slate-700">
                {initials(member.name)}
              </div>
              <div className="flex-1">
                <h3 className="font-semibold text-slate-900 text-lg">{member.name}</h3>
                <p className="text-slate-500 text-sm">{member.role}</p>
              </div>
            </div>
            
            <div>
              <div className="flex items-center justify-between mb-2">
                <span className="text-sm text-slate-600">{tasks} tasks assigned</span>
                <span className="text-sm font-semibold text-slate-900">{tasks}/{capacity} Tasks</span>
              </div>
              <div className="w-full bg-slate-200 rounded-full h-2.5 overflow-hidden">
                <motion.div
                  initial={{ width: 0 }}
                  animate={{ width: `${Math.min(workload, 100)}%` }}
                  transition={{ duration: 0.8, delay: index * 0.1 + 0.2, ease: "easeOut" }}
                  className={`h-full rounded-full ${getProgressColor(workload)}`}
                />
              </div>
            </div>
          </motion.div>
        );
      }) : (
        <motion.div
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          className="col-span-full text-center py-12 text-slate-500"
        >
          No team members yet. Add team members to track workload.
        </motion.div>
      )}
    </div>
  );
}
