'use client';

import { useMemo, useState } from 'react';
import { Plus, Search, Filter, Trash2, Edit, MoreVertical, Sparkles } from 'lucide-react';
import clsx from 'clsx';
import { Card, CardContent, CardHeader, CardTitle } from './Card';
import Modal from './Modal';
import { motion, AnimatePresence } from 'framer-motion';

const statusTone = {
  Planned: 'bg-slate-100 text-slate-700 dark:bg-slate-800/50 dark:text-slate-300 border border-slate-200 dark:border-slate-700',
  'In Progress': 'bg-blue-50 text-blue-700 dark:bg-blue-900/30 dark:text-blue-300 border border-blue-200 dark:border-blue-800',
  'At Risk': 'bg-amber-50 text-amber-700 dark:bg-amber-900/30 dark:text-amber-300 border border-amber-200 dark:border-amber-800',
  Completed: 'bg-emerald-50 text-emerald-700 dark:bg-emerald-900/30 dark:text-emerald-300 border border-emerald-200 dark:border-emerald-800',
  'On Hold': 'bg-orange-50 text-orange-700 dark:bg-orange-900/30 dark:text-orange-300 border border-orange-200 dark:border-orange-800',
  Cancelled: 'bg-red-50 text-red-700 dark:bg-red-900/30 dark:text-red-300 border border-red-200 dark:border-red-800',
};

export default function ProjectDashboard({
  projects = [],
  selectedProjectId,
  onSelect = () => {},
  onCreate = async () => {},
  onDelete = () => {},
}) {
  const [form, setForm] = useState({ name: '', studio: 'Core Studio', dueDate: '', color: '#3b82f6' });
  const [showForm, setShowForm] = useState(false);
  const [submitting, setSubmitting] = useState(false);
  const [search, setSearch] = useState('');

  const filteredProjects = useMemo(() => {
    if (!search.trim()) return projects;
    return projects.filter((project) =>
      project.name?.toLowerCase().includes(search.trim().toLowerCase()),
    );
  }, [projects, search]);

  const handleSubmit = async (event) => {
    event.preventDefault();
    if (!form.name.trim()) return;
    try {
      setSubmitting(true);
      await onCreate(form);
      setForm({ name: '', studio: 'Core Studio', dueDate: '', color: '#3b82f6' });
      setShowForm(false);
    } finally {
      setSubmitting(false);
    }
  };

  return (
    <>
      <Card hover={false}>
        <CardHeader>
          <div className="flex items-center justify-between">
            <div className="flex items-center gap-3">
              <CardTitle>Projects</CardTitle>
              <span className="px-2.5 py-1 text-xs font-semibold bg-primary/10 text-primary rounded-full">
                {filteredProjects.length}
              </span>
            </div>
            <div className="flex items-center gap-2">
              <div className="relative">
                <Search size={16} className="absolute left-3 top-1/2 -translate-y-1/2 text-muted-foreground" />
                <input
                  className="w-full bg-muted/50 border border-border/50 pl-10 pr-4 py-2 rounded-xl text-sm focus:ring-2 focus:ring-primary/20 focus:border-primary/50 focus:outline-none transition-all"
                  placeholder="Search projects"
                  value={search}
                  onChange={(event) => setSearch(event.target.value)}
                />
              </div>
              <motion.button 
                whileHover={{ scale: 1.05 }}
                whileTap={{ scale: 0.95 }}
                type="button" 
                className="p-2.5 rounded-xl hover:bg-muted/80 text-muted-foreground hover:text-foreground transition-colors"
              >
                <Filter size={18} />
              </motion.button>
              <motion.button 
                whileHover={{ scale: 1.02 }}
                whileTap={{ scale: 0.98 }}
                onClick={() => setShowForm(true)} 
                className="flex items-center gap-2 px-4 py-2.5 bg-primary text-primary-foreground rounded-xl font-medium text-sm hover:bg-primary/90 transition-colors shadow-sm"
              >
                <Plus size={18} />
                <span className="hidden sm:inline">New Project</span>
              </motion.button>
            </div>
          </div>
        </CardHeader>
        <CardContent>
          <div className="overflow-x-auto">
            <table className="w-full text-sm">
              <thead>
                <tr className="border-b border-border/50">
                  <th className="px-6 py-3 text-left text-xs font-semibold text-muted-foreground uppercase tracking-wider">Project</th>
                  <th className="px-6 py-3 text-left text-xs font-semibold text-muted-foreground uppercase tracking-wider">Status</th>
                  <th className="px-6 py-3 text-left text-xs font-semibold text-muted-foreground uppercase tracking-wider">Progress</th>
                  <th className="px-6 py-3 text-right text-xs font-semibold text-muted-foreground uppercase tracking-wider">Actions</th>
                </tr>
              </thead>
              <tbody>
                <AnimatePresence mode="popLayout">
                  {filteredProjects.length ? (
                    filteredProjects.map((project, index) => (
                      <motion.tr
                        key={project._id}
                        initial={{ opacity: 0, y: 10 }}
                        animate={{ opacity: 1, y: 0 }}
                        exit={{ opacity: 0, x: -10 }}
                        transition={{ duration: 0.2, delay: index * 0.05 }}
                        className={clsx(
                          'border-b border-border/50 transition-all cursor-pointer group',
                          selectedProjectId === project._id ? 'bg-muted/50' : 'hover:bg-muted/30'
                        )}
                        onClick={() => onSelect(project._id)}
                      >
                        <td className="px-6 py-4">
                          <div className="flex items-center gap-3">
                            <div className="w-2 h-2 rounded-full" style={{ backgroundColor: project.color || '#3b82f6' }} />
                            <span className="font-medium text-foreground">{project.name}</span>
                          </div>
                        </td>
                        <td className="px-6 py-4">
                          <span className={clsx('px-3 py-1.5 text-xs font-medium rounded-lg', statusTone[project.status])}>
                            {project.status}
                          </span>
                        </td>
                        <td className="px-6 py-4">
                          <div className="flex items-center gap-3">
                            <div className="h-2 flex-1 rounded-full bg-muted overflow-hidden">
                              <motion.div
                                initial={{ width: 0 }}
                                animate={{ width: `${project.progress || 0}%` }}
                                transition={{ duration: 0.8, ease: 'easeOut' }}
                                className="h-full rounded-full"
                                style={{ backgroundColor: project.color || '#3b82f6' }}
                              />
                            </div>
                            <span className="text-xs font-semibold text-muted-foreground min-w-[3ch]">{project.progress || 0}%</span>
                          </div>
                        </td>
                        <td className="px-6 py-4 text-right">
                          <div className="flex items-center justify-end gap-1 opacity-0 group-hover:opacity-100 transition-opacity">
                            <motion.button 
                              whileHover={{ scale: 1.1 }}
                              whileTap={{ scale: 0.9 }}
                              className="p-2 rounded-lg hover:bg-muted text-muted-foreground hover:text-foreground transition-colors"
                            >
                              <Edit size={16} />
                            </motion.button>
                            <motion.button 
                              whileHover={{ scale: 1.1 }}
                              whileTap={{ scale: 0.9 }}
                              onClick={(e) => { e.stopPropagation(); onDelete(project._id); }} 
                              className="p-2 rounded-lg hover:bg-destructive/20 text-muted-foreground hover:text-destructive transition-colors"
                            >
                              <Trash2 size={16} />
                            </motion.button>
                          </div>
                        </td>
                      </motion.tr>
                    ))
                  ) : (
                    <tr>
                      <td colSpan={4} className="py-16 text-center">
                        <div className="flex flex-col items-center gap-3">
                          <div className="w-12 h-12 rounded-2xl bg-muted/50 flex items-center justify-center">
                            <Sparkles className="w-6 h-6 text-muted-foreground" />
                          </div>
                          <p className="text-sm font-medium text-muted-foreground">No projects found</p>
                        </div>
                      </td>
                    </tr>
                  )}
                </AnimatePresence>
              </tbody>
            </table>
          </div>
        </CardContent>
      </Card>

      <Modal title="Create New Project" open={showForm} onClose={() => setShowForm(false)}>
        <form onSubmit={handleSubmit} className="space-y-5">
          <div>
            <label className="text-sm font-semibold text-foreground">Project Name</label>
            <input
              required
              placeholder="e.g., Downtown Office Renovation"
              value={form.name}
              onChange={(e) => setForm({ ...form, name: e.target.value })}
              className="w-full mt-2 bg-muted/50 border border-border/50 focus:ring-2 focus:ring-primary/20 focus:border-primary/50 focus:outline-none rounded-xl px-4 py-2.5 transition-all"
            />
          </div>
          <div>
            <label className="text-sm font-semibold text-foreground">Studio</label>
            <input
              placeholder="e.g., Core Studio"
              value={form.studio}
              onChange={(e) => setForm({ ...form, studio: e.target.value })}
              className="w-full mt-2 bg-muted/50 border border-border/50 focus:ring-2 focus:ring-primary/20 focus:border-primary/50 focus:outline-none rounded-xl px-4 py-2.5 transition-all"
            />
          </div>
          <div className="grid grid-cols-2 gap-4">
            <div>
              <label className="text-sm font-semibold text-foreground">Due Date</label>
              <input
                type="date"
                value={form.dueDate}
                onChange={(e) => setForm({ ...form, dueDate: e.target.value })}
                className="w-full mt-2 bg-muted/50 border border-border/50 focus:ring-2 focus:ring-primary/20 focus:border-primary/50 focus:outline-none rounded-xl px-4 py-2.5 transition-all"
              />
            </div>
            <div>
              <label className="text-sm font-semibold text-foreground">Accent Color</label>
              <input
                type="color"
                value={form.color}
                onChange={(e) => setForm({ ...form, color: e.target.value })}
                className="w-full h-[42px] mt-2 cursor-pointer rounded-xl border border-border/50"
              />
            </div>
          </div>
          <div className="flex justify-end gap-3 pt-4">
            <motion.button 
              whileHover={{ scale: 1.02 }}
              whileTap={{ scale: 0.98 }}
              type="button" 
              onClick={() => setShowForm(false)} 
              className="px-5 py-2.5 rounded-xl border border-border/50 hover:bg-muted/50 font-medium text-sm transition-colors"
            >
              Cancel
            </motion.button>
            <motion.button 
              whileHover={{ scale: 1.02 }}
              whileTap={{ scale: 0.98 }}
              type="submit" 
              disabled={submitting} 
              className="flex items-center gap-2 px-5 py-2.5 bg-primary text-primary-foreground rounded-xl font-medium text-sm hover:bg-primary/90 transition-colors shadow-sm disabled:opacity-50"
            >
              <Plus size={18} />
              {submitting ? 'Creating...' : 'Create Project'}
            </motion.button>
          </div>
        </form>
      </Modal>
    </>
  );
}