'use client';

import { useEffect, useMemo, useState } from 'react';
import { BadgeCheck, Loader2, Plus, Trash2 } from 'lucide-react';
import clsx from 'clsx';
import { api } from '../lib/api';
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from './Card';
import { motion, AnimatePresence } from 'framer-motion';

const priorityClasses = {
  High: 'border-red-500/50 bg-red-500/10 text-red-500',
  Medium: 'border-yellow-500/50 bg-yellow-500/10 text-yellow-500',
  Low: 'border-green-500/50 bg-green-500/10 text-green-500',
};

export default function TaskPanel({
  project,
  teamMembers = [],
  onProjectUpdate,
  onTeamRefresh = () => {},
}) {
  const [tasks, setTasks] = useState([]);
  const [loading, setLoading] = useState(false);
  const [form, setForm] = useState({ name: '', assigneeId: '', priority: 'Medium' });
  const [submitting, setSubmitting] = useState(false);
  const [busyTaskId, setBusyTaskId] = useState(null);

  useEffect(() => {
    let ignore = false;
    const load = async () => {
      if (!project?._id) {
        setTasks([]);
        return;
      }
      setLoading(true);
      try {
        const data = await api.getTasks(project._id);
        if (!ignore) setTasks(data);
      } catch (error) {
        console.error('Failed to load tasks', error);
      } finally {
        if (!ignore) setLoading(false);
      }
    };
    load();
    return () => { ignore = true; };
  }, [project?._id]);

  const summary = useMemo(() => {
    const completed = tasks.filter((task) => task.status === 'Complete').length;
    return { completed, total: tasks.length };
  }, [tasks]);

  const pushProjectUpdate = (updatedProject, nextTasks) => {
    if (!updatedProject || !onProjectUpdate) return;
    const completed = nextTasks.filter((task) => task.status === 'Complete').length;
    onProjectUpdate({
      ...updatedProject,
      taskSummary: { total: nextTasks.length, completed },
    });
  };

  const handleSubmit = async (event) => {
    event.preventDefault();
    if (!project?._id || !form.name.trim()) return;
    try {
      setSubmitting(true);
      const payload = { name: form.name, projectId: project._id, assigneeId: form.assigneeId || undefined, priority: form.priority };
      const { task, project: updatedProject } = await api.createTask(payload);
      setTasks((prev) => {
        const next = [task, ...prev];
        pushProjectUpdate(updatedProject, next);
        return next;
      });
      onTeamRefresh();
      setForm({ name: '', assigneeId: '', priority: 'Medium' });
    } catch (error) {
      console.error('Failed to create task', error);
    } finally {
      setSubmitting(false);
    }
  };

  const toggleStatus = async (task) => {
    try {
      setBusyTaskId(task._id);
      const nextStatus = task.status === 'Complete' ? 'Incomplete' : 'Complete';
      const { task: updatedTask, project: updatedProject } = await api.toggleTaskStatus(task._id, nextStatus);
      setTasks((prev) => {
        const next = prev.map((item) => (item._id === task._id ? updatedTask : item));
        pushProjectUpdate(updatedProject, next);
        return next;
      });
      onTeamRefresh();
    } catch (error) {
      console.error('Failed to toggle task', error);
    } finally {
      setBusyTaskId(null);
    }
  };

  const removeTask = async (task) => {
    try {
      setBusyTaskId(task._id);
      const { project: updatedProject } = await api.deleteTask(task._id);
      setTasks((prev) => {
        const next = prev.filter((item) => item._id !== task._id);
        pushProjectUpdate(updatedProject, next);
        return next;
      });
      onTeamRefresh();
    } catch (error) {
      console.error('Failed to delete task', error);
    } finally {
      setBusyTaskId(null);
    }
  };

  return (
    <Card className="flex flex-col h-full">
      <CardHeader>
        <div className="flex items-center justify-between">
          <div>
            <CardTitle>{project ? project.name : 'Tasks'}</CardTitle>
            <CardDescription>
              {project ? `Manage tasks for this project.` : 'Select a project to see its tasks.'}
            </CardDescription>
          </div>
          <div className="text-right">
            <p className="text-sm font-bold text-foreground">{summary.completed}/{summary.total}</p>
            <p className="text-xs text-muted-foreground">Completed</p>
          </div>
        </div>
      </CardHeader>
      <CardContent className="flex-1 flex flex-col gap-4 overflow-y-auto">
        <form onSubmit={handleSubmit} className="flex items-center gap-2">
          <input
            placeholder="Add a new task..."
            value={form.name}
            onChange={(e) => setForm({ ...form, name: e.target.value })}
            disabled={!project || submitting}
            className="flex-1 bg-secondary border-border focus:ring-primary focus:ring-2 focus:outline-none rounded-lg p-2 text-sm"
          />
          <button type="submit" className="btn-primary p-2" disabled={!project || submitting}>
            {submitting ? <Loader2 size={16} className="animate-spin" /> : <Plus size={16} />}
          </button>
        </form>

        <div className="flex-1">
          {loading ? (
            <div className="flex h-full items-center justify-center text-muted-foreground">
              <Loader2 className="animate-spin" />
            </div>
          ) : tasks.length ? (
            <motion.div layout className="space-y-2">
              <AnimatePresence>
                {tasks.map((task) => (
                  <motion.div
                    key={task._id}
                    layout
                    initial={{ opacity: 0, y: -10 }}
                    animate={{ opacity: 1, y: 0 }}
                    exit={{ opacity: 0, x: -20 }}
                    className="flex items-center gap-3 p-3 bg-secondary rounded-lg"
                  >
                    <button onClick={() => toggleStatus(task)} disabled={busyTaskId === task._id}>
                      {busyTaskId === task._id ? (
                        <Loader2 size={16} className="animate-spin text-muted-foreground" />
                      ) : (
                        <motion.div whileHover={{ scale: 1.1 }}>
                          <BadgeCheck
                            size={20}
                            className={clsx(
                              'transition-colors',
                              task.status === 'Complete' ? 'text-green-500' : 'text-muted-foreground/50'
                            )}
                          />
                        </motion.div>
                      )}
                    </button>
                    <div className="flex-1">
                      <p className={clsx('text-sm font-medium', task.status === 'Complete' && 'line-through text-muted-foreground')}>
                        {task.name}
                      </p>
                      <div className="flex items-center gap-2 mt-1">
                        <span className={clsx('text-xs px-2 py-0.5 rounded-full border', priorityClasses[task.priority])}>
                          {task.priority}
                        </span>
                        {task.assignee && (
                          <div className="flex items-center gap-1 text-xs text-muted-foreground">
                            <img src={`https://i.pravatar.cc/16?u=${task.assignee._id}`} className="w-4 h-4 rounded-full" />
                            {task.assignee.name}
                          </div>
                        )}
                      </div>
                    </div>
                    <button onClick={() => removeTask(task)} disabled={busyTaskId === task._id} className="p-1.5 rounded-md hover:bg-destructive/20 hover:text-destructive">
                      <Trash2 size={14} />
                    </button>
                  </motion.div>
                ))}
              </AnimatePresence>
            </motion.div>
          ) : (
            <div className="flex h-full items-center justify-center text-center text-muted-foreground border-2 border-dashed border-border rounded-lg">
              <p>{project ? 'No tasks yet. Add one above!' : 'Select a project.'}</p>
            </div>
          )}
        </div>
      </CardContent>
    </Card>
  );
}