'use client';

import { useEffect, useState } from 'react';
import { api } from '../lib/api';

export const normalizeProject = (project) => ({
  ...project,
  taskSummary: project.taskSummary ?? { total: 0, completed: 0 },
});

export default function useWorkspace() {
  const [projects, setProjects] = useState([]);
  const [teamMembers, setTeamMembers] = useState([]);
  const [loading, setLoading] = useState(true);
  const [projectSyncing, setProjectSyncing] = useState(() => new Set());

  useEffect(() => {
    let cancelled = false;

    const bootstrap = async () => {
      setLoading(true);
      try {
        const [projectsResponse, teamResponse] = await Promise.all([
          api.getProjects(),
          api.getTeamMembers(),
        ]);
        if (!cancelled) {
          setProjects(projectsResponse.map(normalizeProject));
          setTeamMembers(teamResponse);
        }
      } catch (error) {
        console.error('Failed to load workspace data', error);
      } finally {
        if (!cancelled) {
          setLoading(false);
        }
      }
    };

    bootstrap();
    return () => {
      cancelled = true;
    };
  }, []);

  const handleProjectCreate = async (payload) => {
    const project = await api.createProject(payload);
    const normalized = normalizeProject(project);
    setProjects((prev) => [normalized, ...prev]);
    return normalized;
  };

  const handleProjectDelete = async (projectId) => {
    await api.deleteProject(projectId);
    setProjects((prev) => prev.filter((item) => item._id !== projectId));
  };

  const handleProgressUpdate = async (projectId, progressValue) => {
    setProjectSyncing((prev) => {
      const next = new Set(prev);
      next.add(projectId);
      return next;
    });
    try {
      const updated = await api.updateProjectProgress(projectId, progressValue);
      setProjects((prev) =>
        prev.map((project) =>
          project._id === projectId ? normalizeProject({ ...project, ...updated }) : project,
        ),
      );
    } finally {
      setProjectSyncing((prev) => {
        const next = new Set(prev);
        next.delete(projectId);
        return next;
      });
    }
  };

  const handleProjectHydrate = (updatedProject) => {
    if (!updatedProject) return;
    setProjects((prev) =>
      prev.map((project) => (project._id === updatedProject._id ? normalizeProject(updatedProject) : project)),
    );
  };

  const refreshTeam = async () => {
    const list = await api.getTeamMembers();
    setTeamMembers(list);
  };

  const handleTeamCreate = async (payload) => {
    await api.createTeamMember(payload);
    await refreshTeam();
  };

  return {
    projects,
    teamMembers,
    loading,
    projectSyncing,
    handleProjectCreate,
    handleProjectDelete,
    handleProgressUpdate,
    handleProjectHydrate,
    refreshTeam,
    handleTeamCreate,
  };
}
