'use client';

import { useState } from 'react';
import { useTheme } from 'next-themes';
import { motion } from 'framer-motion';
import { Search, Bell, Sun, Moon, Menu, Command } from 'lucide-react';

export default function Header({ setSidebarOpen }) {
  const { theme, setTheme } = useTheme();
  const [searchFocused, setSearchFocused] = useState(false);

  return (
    <header className="sticky top-0 z-20 flex items-center justify-between h-16 px-6 glass border-b border-border/50">
      <div className="flex items-center gap-4 flex-1">
        <button 
          onClick={() => setSidebarOpen(true)} 
          className="md:hidden p-2 -ml-2 text-muted-foreground hover:text-foreground transition-colors"
        >
          <Menu size={22} />
        </button>
        <motion.div 
          className="relative hidden md:block max-w-md w-full"
          animate={{ scale: searchFocused ? 1.02 : 1 }}
          transition={{ duration: 0.2 }}
        >
          <Search className="absolute left-3.5 top-1/2 -translate-y-1/2 text-muted-foreground" size={18} />
          <input
            type="text"
            placeholder="Search projects, tasks, team..."
            onFocus={() => setSearchFocused(true)}
            onBlur={() => setSearchFocused(false)}
            className="w-full pl-11 pr-20 py-2.5 bg-muted/50 border border-border/50 rounded-xl text-sm focus:ring-2 focus:ring-primary/20 focus:border-primary/50 focus:outline-none transition-all placeholder:text-muted-foreground/60"
          />
          <div className="absolute right-3 top-1/2 -translate-y-1/2 flex items-center gap-1 text-xs text-muted-foreground">
            <kbd className="px-1.5 py-0.5 bg-background border border-border/50 rounded text-[10px] font-medium">âŒ˜</kbd>
            <kbd className="px-1.5 py-0.5 bg-background border border-border/50 rounded text-[10px] font-medium">K</kbd>
          </div>
        </motion.div>
      </div>
      <div className="flex items-center gap-2">
        <motion.button
          whileHover={{ scale: 1.05 }}
          whileTap={{ scale: 0.95 }}
          onClick={() => setTheme(theme === 'dark' ? 'light' : 'dark')}
          className="p-2.5 rounded-xl hover:bg-muted/80 text-muted-foreground hover:text-foreground transition-colors"
        >
          {theme === 'dark' ? <Sun size={18} /> : <Moon size={18} />}
        </motion.button>
        <motion.button 
          whileHover={{ scale: 1.05 }}
          whileTap={{ scale: 0.95 }}
          className="p-2.5 rounded-xl hover:bg-muted/80 text-muted-foreground hover:text-foreground transition-colors relative"
        >
          <Bell size={18} />
          <span className="absolute top-1.5 right-1.5 w-2 h-2 bg-red-500 rounded-full ring-2 ring-card"></span>
        </motion.button>
        <div className="w-9 h-9 rounded-xl overflow-hidden ring-2 ring-border/50 hover:ring-primary/50 transition-all cursor-pointer ml-2">
          <img src="https://i.pravatar.cc/36" alt="User" className="w-full h-full object-cover" />
        </div>
      </div>
    </header>
  );
}
