'use client';

import { AlertTriangle, CalendarClock, Activity } from 'lucide-react';
import clsx from 'clsx';

const formatDate = (value) => {
  if (!value) return 'No due date';
  try {
    return new Intl.DateTimeFormat('en', { month: 'short', day: 'numeric' }).format(new Date(value));
  } catch (error) {
    return 'No due date';
  }
};

const getWorkload = (member) => {
  if (typeof member?.workload === 'number') {
    return Math.round(member.workload);
  }
  const capacity = member?.capacity || 5;
  const assigned =
    (Array.isArray(member?.tasks) ? member.tasks.length : member?.taskCount || 0) || 0;
  if (!capacity) {
    return 0;
  }
  return Math.min(100, Math.round((assigned / capacity) * 100));
};

export default function StudioInsights({ projects = [], teamMembers = [] }) {
  const now = Date.now();
  const upcomingWindow = now + 1000 * 60 * 60 * 24 * 7;

  const overdue = projects
    .filter(
      (project) =>
        project?.dueDate &&
        new Date(project.dueDate).getTime() < now &&
        project.status !== 'Completed',
    )
    .sort((a, b) => new Date(a.dueDate) - new Date(b.dueDate));

  const dueSoon = projects
    .filter((project) => {
      if (!project?.dueDate || project.status === 'Completed') return false;
      const time = new Date(project.dueDate).getTime();
      return time >= now && time <= upcomingWindow;
    })
    .sort((a, b) => new Date(a.dueDate) - new Date(b.dueDate));

  const capacityAlerts = teamMembers
    .map((member) => ({ ...member, computedWorkload: getWorkload(member) }))
    .filter((member) => member.computedWorkload >= 90)
    .sort((a, b) => b.computedWorkload - a.computedWorkload);

  const statCards = [
    {
      label: 'Overdue briefs',
      value: overdue.length,
      icon: AlertTriangle,
      accent: 'text-rose-600',
      tone: 'bg-rose-50 border-rose-100',
    },
    {
      label: 'Due this week',
      value: dueSoon.length,
      icon: CalendarClock,
      accent: 'text-amber-600',
      tone: 'bg-amber-50 border-amber-100',
    },
    {
      label: 'Capacity alerts',
      value: capacityAlerts.length,
      icon: Activity,
      accent: 'text-blue-600',
      tone: 'bg-blue-50 border-blue-100',
    },
  ];

  return (
    <section className="surface-card space-y-5">
      <header>
        <p className="text-xs font-semibold uppercase tracking-[0.35em] text-slate-400">Insights</p>
        <h2 className="mt-2 text-2xl font-semibold text-slate-900">Studio pulse</h2>
        <p className="text-sm text-slate-500">auto-generated alerts from live project data.</p>
      </header>

      <div className="grid gap-3 sm:grid-cols-3">
        {statCards.map((card) => {
          const Icon = card.icon;
          return (
            <article
              key={card.label}
              className={clsx(
                'rounded-2xl border px-4 py-3',
                card.tone,
                'flex items-center gap-3 text-sm font-semibold',
              )}
            >
              <Icon size={18} className={card.accent} />
              <div>
                <p className="text-xs uppercase tracking-[0.3em] text-slate-500">{card.label}</p>
                <p className={clsx('text-2xl', card.accent)}>{card.value}</p>
              </div>
            </article>
          );
        })}
      </div>

      <div className="space-y-4">
        <section className="rounded-2xl border border-slate-200 bg-white p-4">
          <div className="flex items-center justify-between">
            <p className="font-semibold text-slate-900">Delivery timeline</p>
            <span className="text-xs text-slate-500">Next 7 days</span>
          </div>
          <div className="mt-3 space-y-3">
            {dueSoon.length || overdue.length ? (
              [...overdue, ...dueSoon].slice(0, 4).map((project) => (
                <div
                  key={project._id}
                  className="flex items-center justify-between gap-3 rounded-xl border border-slate-200 bg-slate-50 px-3 py-2"
                >
                  <div>
                    <p className="font-semibold text-slate-900">{project.name}</p>
                    <p className="text-xs text-slate-500">Due {formatDate(project.dueDate)}</p>
                  </div>
                  <span className="text-xs font-semibold text-slate-500">
                    {project.taskSummary?.completed || 0}/{project.taskSummary?.total || 0} tasks
                  </span>
                </div>
              ))
            ) : (
              <p className="rounded-xl border border-dashed border-slate-300 px-3 py-4 text-center text-sm text-slate-500">
                All upcoming deadlines are on track.
              </p>
            )}
          </div>
        </section>

        <section className="rounded-2xl border border-slate-200 bg-white p-4">
          <div className="flex items-center justify-between">
            <p className="font-semibold text-slate-900">Capacity watchlist</p>
            <span className="text-xs text-slate-500">Target &lt; 90%</span>
          </div>
          <div className="mt-3 space-y-3">
            {capacityAlerts.length ? (
              capacityAlerts.slice(0, 4).map((member) => (
                <div
                  key={member._id}
                  className="flex items-center justify-between gap-3 rounded-xl border border-slate-200 bg-slate-50 px-3 py-2"
                >
                  <div>
                    <p className="font-semibold text-slate-900">{member.name}</p>
                    <p className="text-xs text-slate-500">{member.role}</p>
                  </div>
                  <span className="rounded-full bg-rose-100 px-3 py-1 text-xs font-semibold text-rose-600">
                    {member.computedWorkload}% load
                  </span>
                </div>
              ))
            ) : (
              <p className="rounded-xl border border-dashed border-slate-300 px-3 py-4 text-center text-sm text-slate-500">
                No team members are over capacity.
              </p>
            )}
          </div>
        </section>
      </div>
    </section>
  );
}
